import React, {useState} from "react";
import { storiesOf } from "@storybook/react";

import { Modal } from "../components/Modal/Modal";

const stories = storiesOf('Modal', module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })

stories.add('App', () => {
    const [isDisplayed, setModalDisplay]  = useState(false);
    function openModal (e) {
        e.preventDefault()
        setModalDisplay(true)
    }
    return (
        <div className="App">
        {}
        {isDisplayed ? (<Modal setModalState={setModalDisplay} title={'Je suis une modal'} />) : null}
        </div>
    );
})